.home{
    font-size: 60px;
    color: rgb(4, 74, 143);
    padding: 5px 10px;
    font-style: italic;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 10px;
    background-color: aliceblue;
}
.reload{
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
    outline: 2px solid rgb(4, 74, 143);
    padding: 10px 20px;
    position: relative;
    overflow: hidden;
    transition: color .8s;
    background: none;
    margin-bottom: 10px;
    border: none;
}
.reload:hover{
    color: rgb(4, 74, 143);
}
.reload::before{
    cursor: pointer;
    content: '';
    position: absolute;
    top: 0;
    left: -25px;
    width: 0;
    height: 100%;
    background-color: white;
    transform: skewX(35deg);
    z-index: -1;
    transition: width .8s;
}
.reload:hover::before{
    cursor: pointer;
    width: 150%;
}
.navpaginado{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.paginado{
    position: relative;
    display: inline-block;
}
.btnpaginado{
    cursor: pointer;
    display: block;
    color: white;
    text-align: center;
    text-decoration: none;
    padding: 5px 8px;
    margin: 3px;
    background: none;
}
.btnpaginado:hover{
    background-color: rgb(4, 74, 143);
    transition: all 0.3s;
}
.cardDiv{
    width: 150px;
    height: 200px;
    background-color: rgb(4, 74, 143);
    border-radius: 10px;
    outline: 1px solid powderblue;
    outline-offset: -8px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, -1);
    position: relative;
    overflow: hidden;
}
.cardImg{
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: .6;
    transition: transfrom .75s;
}
.card:hover .cardImg {
    transform: scale(1.15);
}
.cardText{
    position: absolute;
    left: 15px;
    bottom: 15px;
    font-size: small;
    text-shadow: 0 0 1px rgba(0, 0, 0, -5);
}